import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProjectsService } from 'src/app/services/projects.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
projectId;
changeparam;
project_item;
  constructor(private route: ActivatedRoute, private projectSer: ProjectsService, private router: Router) { }

  ngOnInit() {
    this.projectId = this.route.snapshot.paramMap.get('id');

    this.project_item = this.projectSer.projects[this.projectId];
    // console.log(this.projectId);
    // console.log(this.project_item);
    if (this.project_item === undefined ) {
      this.router.navigateByUrl('');
    }
  }

}
